.Header {
    width: 100%;
    padding: 20px;
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    background: linear-gradient(-45deg, var(--main-color), var(--main-color-light));
    color: white;
  }
  
  .Header a {
    text-decoration: none;
    color: inherit;
  }

  .Header__profile {
  }
.footer {
  width: 100%;
  padding: 20px;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  background: linear-gradient(-45deg, var(--main-color), var(--main-color-light));
  color: white;
}

.MovieCard {
    text-align: center;
    background-color: #333333;
    border: 1px solid black;
    border-radius: 15px;
    overflow: hidden;
  }

.MovieCard-image {
  width: 100%;
  height: 250px;
  position: relative;
}

.MovieCard-image img {
  height: 100%;
  width: 100%;
}

a {
  text-decoration: none;
}

.MovieCard-title {
  position: absolute;
  background-color: rgba(0,0,0,0.6);
  display: block;
  width: 100%;
  z-index: 1;
  padding: 10px 20px;
  text-align: left;
  bottom: 0;
}

.MovieCard-title p:last-child {
  margin-top: 5px;
  font-size: 15px;
}

.MovieCard-title h1 {
  color: #D0CCBF;
  height: 100%;
}

.MovieCard-title p {
  color: #898C8D; 
}

.MovieCard-body {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 20px;
  height: 222px;
}

.MovieCard-body p {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.MovieCard-body p:not(:first-child) {
  margin-top: 10px;
}

.MovieCard-body p:not(:last-child) {
  padding-bottom: 10px;
  border-bottom: 1px solid  rgb(68, 68, 68);
}

.MovieCard-body p span:first-child {
  font-size: .7rem;
  text-transform: uppercase;
  color: rgb(158, 158, 158);
  text-align: left;
}

.MovieCard-body p span:last-child {
  font-size: 0.8em;
  text-align: right;
  color: rgb(255, 152, 0);
  text-transform: capitalize;
}

.fontIcon {
  margin-left: 10px;
}
.back-link {
    display: block;
    margin: 2rem 0 0 0;
    text-transform: uppercase;
    font-size: 1rem;
    color: rebeccapurple;
  }
  
  .back-link:hover {
    font-weight: bold;
    color: palevioletred;
  }
.form-cards {
    background: linear-gradient(-45deg, var(--main-color), var(--main-color-light));
    color: white;
    border-radius: 15px;
    margin-bottom: 10px;
    padding: 20px 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  
  .form-cards p {
    font-size: 2rem;
    margin-bottom: 10px;
  }
  
  .form-cards input, .form-cards select {
    background-color: white;
    margin-top: 5px;
    padding: 10px;
    font-size: 1rem;
    border: none;
    max-width: 500px;
  }
  
.card-container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
    justify-items: center;
    text-align: center;
    margin: 20px 0;
}

.card-container > h1 {
    color: #333;
    padding: 20px;
}
.Login {
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background: linear-gradient(-45deg, var(--main-color), var(--main-color-light));
}

.Login__Wrapper {
    max-width: 320px;
    padding: 3rem 2rem;
    background-color: white;
    box-shadow: 3px 3px 7px rgba(0, 0, 0, .5);
}

.Login__Form {
    display: grid;
    grid-gap: 2rem;
}

.Login__Form .button {
    -webkit-align-self: flex-end;
            align-self: flex-end;
}

.Login__Logo {
    justify-self: center;
    -webkit-align-self: center;
            align-self: center;
    max-width: 70%;
}

@media only screen and (min-width: 600px) {
    .Login__Wrapper {
        max-width: 400px;
        padding: 4rem 2rem;
    }
}

.InputIcon-icon {
    padding-right: 3px;
    padding-bottom: 2px;
}

.InputIcon-icon + input::-webkit-input-placeholder{
    font-size: .9rem;
}

.InputIcon-icon + input::-ms-input-placeholder{
    font-size: .9rem;
}

.InputIcon-icon + input::placeholder{
    font-size: .9rem;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; 
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.section {
  padding: 20px;
}

:root {
  --main-color-light: rgb(0, 60, 255);
  --main-color: rgb(27, 250, 231);
}
